import { useCallback, useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useAdmissionOngoingApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionOngoingApplicationCard from "./AdmissionOngoingApplicationCard";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const AdmissionOngoingApplication = ({ aid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { admissionOngoingApplication, admissionOngoingApplicationLoading } =
    useAdmissionOngoingApplication({
      data: {
        aid: aid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !aid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplication?.list?.length) {
      let obj = nextPage(admissionOngoingApplication?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplication?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationLoading &&
      admissionOngoingApplication?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission`
        : `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("ongoing_applications")}</h6>
            </div>

            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "220px",
                    }}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
            position: "relative",
          }}
        >
          {admissionOngoingApplication?.list?.map((application, index) =>
            admissionOngoingApplication?.list?.length === index + 1 ? (
              <div key={application?._id} ref={ref}>
                <AdmissionOngoingApplicationCard application={application} />
              </div>
            ) : (
              <AdmissionOngoingApplicationCard
                application={application}
                key={application?._id}
              />
            )
          )}
          {admissionOngoingApplicationLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionOngoingApplication?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No ongoing application found related this search."
              />
            )
          ) : (
            admissionOngoingApplication?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No application created."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionOngoingApplication;
