import style from "../OngoingAdmission/OngoingAdmission.module.css";
import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { Link, useLocation, useParams } from "react-router-dom";

const AdmissionOngoingApplicationCard = ({ application }) => {
  const params = useParams();
  const getQuery = useLocation();
  //
  return (
    <>
      <Link
        to={
          getQuery.state?.urlOffSet === "Institute"
            ? `/q/${params.username}/institute/member/admission?type=enquiries`
            : `/q/${params.username}/member/staff/admission?type=enquiries`
        }
        state={{
          ...getQuery.state,
          appId: application?._id,
        }}
      >
        <div className={style.ongoing_card}>
          <div className={style.ongoing_card_inner}>
            <img
              alt="Ongoing App"
              src={`${assestsUserAdmissionUrl}/application.svg`}
            />
            <div className={style.ongoing_card_text}>
              <h6>{application?.applicationName ?? ""}</h6>
              <p>{application?.applicationDepartment?.dName ?? ""}</p>
            </div>
          </div>
        </div>
      </Link>
    </>
  );
};

export default AdmissionOngoingApplicationCard;
